<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="配置名称"
          size="small"
          style="width: 140px"
          v-model="searchParams.configName"
          clearable
        ></el-input>
        <el-input
          placeholder="配置键名"
          size="small"
          style="width: 140px"
          v-model="searchParams.configKey"
          clearable
        ></el-input>
        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
      <div>
        <el-button type="primary" size="small" @click="add()">添加</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" class="yb-table-expand">
            <el-form-item label="创建人">
              <span>{{ props.row.createBy }}</span>
            </el-form-item>
            <el-form-item label="创建时间">
              <span>{{ props.row.createTime }}</span>
            </el-form-item>
            <el-form-item label="修改人">
              <span>{{ props.row.updateBy }}</span>
            </el-form-item>
            <el-form-item label="修改人时间">
              <span>{{ props.row.updateTime }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="configName" label="配置名称"></el-table-column>
      <el-table-column align="center" prop="configKey" label="配置键名"></el-table-column>
      <el-table-column align="center" prop="configValue" label="配置值"></el-table-column>
      <el-table-column align="center" label="操作">
        <div slot-scope="s">
          <el-button type="primary" size="small" @click="edit(s.row)">编辑</el-button>
          <el-button type="danger" size="small" @click="removeItem(s.row)">删除</el-button>
        </div>
      </el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getConfigList"
      ref="pagination"
      @returnData="returnData"
    />
    <ConfigEdit
      :title="titie"
      ref="edit"
      :showEditDialog="showEditDialog"
      @close="showEditDialog = false"
      @success="refresh"
    />
  </div>
</template>

<script>
import { getConfigList, deleteConfig } from "@/api/sys/sysManage";
import { getDateList } from "@/utils/date";
import { resetObject } from "@/utils/common";
import ConfigEdit from "./ConfigEdit";
export default {
  components: { ConfigEdit },
  data() {
    return {
      searchParams: {
        configName: "",
        configKey: ""
      },
      getConfigList: getConfigList,
      titie: "添加",
      showEditDialog: false,
      tableData: []
    };
  },
  created() {},
  methods: {
    edit(row) {
      this.titie = "修改";
      this.showEditDialog = true;
      this.$refs.edit.update(row.id, row);
    },
    add() {
      this.titie = "添加";
      this.showEditDialog = true;
      this.$refs.edit.add();
    },
    removeItem(item) {
      this.$confirm("此操作将永久删除该配置吗, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteConfig(item.id).then(res => {
            if (res.code == 0) {
              this.$message.success("删除成功!");
              this.refresh();
              return;
            }
            this.$message.error(res.msg);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    returnData(data) {
      // console.log(data);
      this.tableData = data;
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    }
  }
};
</script>
<style  scoped>
</style>