<template>
  <div>
    <el-dialog
      @opened="openDialog"
      :width="dialogWidth"
      :title="title"
      :visible.sync="showEditDialog"
      :show-close="false"
      :close-on-click-modal="false"
    >
      <el-form
        :model="forms"
        :rules="rules"
        ref="editForms"
        class="edit-forms"
        label-position="left"
        :label-width="labelWidth"
      >
        <el-form-item label="配置名称" prop="configName">
          <el-input v-model="forms.configName" placeholder="配置名称"></el-input>
        </el-form-item>
        <el-form-item label="配置键名" prop="configKey">
          <el-input v-model="forms.configKey" placeholder="配置键名"></el-input>
        </el-form-item>
        <el-form-item label="配置值" prop="configValue">
          <el-input v-model="forms.configValue"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="$emit('close')">取 消</el-button>
        <el-button type="primary" @click="submit">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Required } from "@/utils/validateRules";
import { editConfig } from "@/api/sys/sysManage";
import { fillerLeft, resetObject } from "@/utils/common";

export default {
  props: {
    title: {
      type: String,
      default: "编辑"
    },
    dialogWidth: {
      type: String,
      default: "700px"
    },
    labelWidth: {
      type: String,
      default: "120px"
    },
    showEditDialog: Boolean
  },
  data() {
    return {
      forms: {
        configName: "",
        configKey: "",
        configValue: ""
      },
      rules: {
        configName: Required,
        configKey: Required,
        configValue: Required
      }
    };
  },
  methods: {
    submit() {
      this.$refs.editForms.validate(valid => {
        if (valid) {
          editConfig(this.forms).then(data => {
            if (data.code == 0) {
              this.$message.success("更新成功！");
              this.$emit("success");
              return;
            }
            this.$message.error(data.msg);
          });
          return;
        }
        this.$message.error("请按照提示正确填写内容！");
        return false;
      });
    },
    openDialog() {},
    add() {
      resetObject(this.forms);

      this.$set(this.forms, "id", null);
      this.$refs.editForms.resetFields();
    },
    update(id, data) {
      //可以外部填充回写做编辑用，也可以请求详情接口填充表单
      this.$set(this.forms, "id", id);
      fillerLeft(this.forms, data);
    }
  }
};
</script>
